import uniPagination from '@dcloudio/uni-ui/lib/uni-pagination/uni-pagination.vue'
	export default {
		data() {
			return {
			pageNum:5,
			currentPage:1,
			SearchInput:"",
			total:0,
			searchList:[],
			list:[],
			branch:0,
			pageSize:10,//每夜条数
			friendList:[],
			}  
		},
		onLoad(option) { 
			var _self =this
			_self.getInfo()
		},
		onPullDownRefresh:function(){
			var _self = this
			_self.getInfo()
		},
		methods: {
			changePage(type){
				//console.log(type.current)
				this.currentPage = type.current
				this.getInfo()
			},
			getInfo:function(){
				var _self =this
				 uni.request({
				 	url:_self.$api+"dockingManager/tradeWorkQuery",
					data:{optionId:uni.getStorageSync("openId"),branch:_self.currentPage,live:_self.pageSize},
					method:"GET",
					success:function(res){
						console.log(res)
						_self.friendList = res.data
						if(res.data == ""){
							_self.branch = []
						}else{
							_self.branch = res.data[0].branch
						}
						 uni.stopPullDownRefresh();  
					}
				 })
			},
			/**
			 * 删除
			 */
			tradeWorkDelete:function(id,tube){
				var _self = this
				uni.showModal({
				title: '提示',
				content: '是否删除此用户',
				success: function (res) {
					if (res.confirm) {
						uni.request({
							url:_self.$api+"dockingManager/tradeWorkDelete",
								data:{id:id,optionId:uni.getStorageSync("openId")},
								method:"GET",
								success:function(res){
									if(res.data == 98){
										uni.showToast({
											title:"没有权限",
											icon:"none"
										})
										return false
									}
									else if(res.data == 99){
										uni.showToast({
											title:"您尚未登录",
											icon:"none"
										})
										return false
									}
									else if(res.data == 1){
										uni.showToast({
											title:"删除成功"
										})
										_self.getInfo()
									}else{
										uni.showToast({
											title:"删除失败",
											icon:"none"
										})
									}	
								}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});	 
			},
			/**
			 * 授权
			 */
			tradeTubeAdd:function(id,tube){
				console.log(tube)
				console.log(id)
					var _self = this
					uni.showModal({
					title: '提示',
					content: '是否为此用户授权或取消管理权限',
					success: function (res) {
						if (res.confirm) {
							uni.request({
								url:_self.$api+"dockingManager/tradeTubeAdd",
									data:{id:id,optionId:uni.getStorageSync("openId"),tube:tube},
									method:"GET",
									success:function(res){
										if(res.data == 98){
											uni.showToast({
												title:"没有权限",
												icon:"none"
											})
											return false
										}
										else if(res.data == 99){
											uni.showToast({
												title:"您尚未登录",
												icon:"none"
											})
											return false 
										}
										else if(res.data == 1 && tube==0){
											uni.showToast({
												title:"取消授权成功"
											})
											_self.getInfo()
										}
										else if(res.data == 1 && tube==1){
											uni.showToast({
												title:"授权成功"
											})
											_self.getInfo()
										}else{
											uni.showToast({
												title:"授权失败",
												icon:"none"
											})
										}	
									}
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});	 
			},
		},
		watch:{
			
		},
		filters:{
			rTime:function(data){
				if(data==null||data==undefined){
					return data
				}else{
					return data.slice(0,11)
				}
			}
		},
		components:{uniPagination}
	} 
